<template>
  <div class="container" :style="{height:contentHeight}">
    <mt-header :title="pageTitle"></mt-header>

    <div class="content-qc">
      <div class="qc-title">
        <span class="qc-title-h1">仅限业主之间交流房屋相关问题</span>
        <span class="qc-title-h2">禁止广告</span>
        <span class="qc-title-h3">禁止任何人, 以任何形式, 发送任何广告！！！</span>
      </div>
      <img class="qc_img" :src="imgUrl" alt="二维码">

      <div class="qc-note">
        <span class="qc-note-h1">长按图片保存到相册后</span>
        <span class="qc-note-h1">使用微信扫一扫入群</span>
        <mt-button
            class="qc-note-btn"
            type="primary"
            @click="clickWCNo"
            :data-clipboard-text="wechatNO"
            size="normal">
          微信号（点击复制）：{{wechatNO}}
        </mt-button>

        <span class="qc-note-h3"><b>注：</b>二维码定时更新，如失效请添加群主微信号，拉你进群。</span>
      </div>
    </div>
  </div>
</template>

<script>
import {MessageBox} from "mint-ui";
import clipboard from "clipboard";

const baseHeight = 0
export default {
  name: "WechatQC",
  data() {
    return {
      pageTitle: '欢迎加入 七星长安-业主群',
      wechatNO:'813836993',
      imgUrl: require('@/assets/imgs/qxca_wx_qc.jpg'),
      contentHeight: window.innerHeight - baseHeight
    }
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.contentHeight = window.innerHeight - baseHeight
      })()
    }
  },
  methods: {
    clickWCNo() {
      console.log('clickWCNo')
      let cd = new clipboard('.qc-note-btn');
      cd.on('success', function ()  {
        MessageBox.alert('微信号复制成功')
        cd.destroy()
      })
      cd.on('error', function () {
        // vant.Notify({ type: 'warning', message: '该浏览器不支持自动复制', duration: 1000 });
        MessageBox.alert('该浏览器不支持自动复制')
        cd.destroy()
      })
    }
  }
}
</script>

<style scoped>
.container {
}

.content-qc {
  width: 100%;
}

.qc-title {
  display: -webkit-flex; /* Safari */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  height: 100px;
  margin-bottom: 20px;
  padding-top: 10px;
}

.qc-title-h1 {
  font-size: 26px;
}

.qc-title-h2 {
  font-size: 38px;
  color: red;
}

.qc-title-h3 {
  font-size: 18px;
}

.qc_img {
  width: 100%;
}

.qc-note {
  display: -webkit-flex; /* Safari */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.qc-note-h1 {
  font-size: 18px;

  margin-bottom: 10px;
}

.qc-note-h3 {
  font-size: 14px;
  margin-bottom: 10px;
}

.qc-note-btn {
  width: 95%;
}
</style>